// –ø—Ä–æ–≤–µ—Ä–∫–∞ —Å–µ—Ä–≤–µ—Ä–∞
export const checkServer = async () => {
    const api = constant.serverAPI;
    try {
        const response = await fetch(`${api}/status`, {
            method: "GET",
            headers: {
                "Content-Type": "application/json",
            },
        });
        const result = await response.statusText;
        console.log('Incoming status: ', result)
        return true;
    } catch (error) {
        console.log(error);
        return false;
    }


}